// App.js
import './App.css';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faCamera, faPen } from '@fortawesome/free-solid-svg-icons';
import Photos from './Photos'; // Import Photos component
import Writing from './Writing'; 
import React from 'react';
import ReactMarkdown from 'react-markdown'; // Import react-markdown

function App() {
  return (
    <Router>
      <div className="App">
        <div className="container">
          {/* Header Section */}
          <header>
            <div className="header-content">
              <Link to="/" className="home-link">
                <h1>Marcus Brady</h1>
              </Link>
              <div className="icon-group">
                <Link to="/photos">
                  <FontAwesomeIcon icon={faCamera} className="camera-icon" />
                </Link>
                <Link to="/writing">
                  <FontAwesomeIcon icon={faPen} className="pen-icon" />
                </Link>
              </div>
            </div>
            <p>Coffee and Chemistry Enthusiast.</p>
          </header>

          {/* Main Routes */}
          <Routes>
            <Route path="/" element={<Home />} />
            <Route path="/photos" element={<Photos />} />
            <Route path="/writing" element={<Writing />} />
          </Routes>
        </div>
      </div>
    </Router>
  );
}

function Home() {
  // List of updates with date information in Markdown format
  const updates = [
    // Uncomment and modify if needed
    // { date: '23-09-2024', heading: 'Starting PhD', body: 'WIP' },
    {
      date: '09-11-2024',
      heading: 'A month in on PhD',
      body: 'First off, I finally got confirmation of my [submitted thesis](https://etheses.whiterose.ac.uk/35738/) so another graduation to look forward to. Secondly, I have settled into my PhD, with over a month of experience under my belt. It is going great so far.',
    },
    {
      date: '01-10-2024',
      heading: 'Starting PhD',
      body: 'Started a PhD in Chemistry in the Crespo-Otero Research Group at UCL to work on machine learning accelerated non-adiabatic dynamics, super exciting stuff.',
    },
    {
      date: '01-09-2024',
      heading: 'Finish MScR',
      body: 'Finished a year-long project in the Evans Group at the Wolfson Atmospheric Chemistry Laboratory, resulting in the completion of my Masters by Research in Chemistry.',
    },
    {
      date: '14-09-2024',
      heading: 'Deployed this GH Pages',
      body: 'Version 1.0 of GitHub Pages for my personal updates, with initial photos from my Northumberland trip in August 2024 on the [photos page](/photos) and some basic updates.',
    },
  ];

  // Function to convert DD-MM-YYYY string to a Date object for sorting
  const convertDateStringToDate = (dateString) => {
    const [day, month, year] = dateString.split('-');
    return new Date(`${year}-${month}-${day}`);
  };

  // Sort updates by date (newest first)
  const sortedUpdates = updates.sort((a, b) => {
    return convertDateStringToDate(b.date) - convertDateStringToDate(a.date);
  });

  return (
    <div>
      {/* Introduction Section */}
      <section>
        <h2>Introduction</h2>
        <p className="intro">
          Hi, Im a PhD student at UCL, where I currently investigate on how to make nonadiabtic dynamics calculations quicker (through ML). When Im not coding or waiting for my QM jobs to finish running, you can usually find me at a caf, watching a movie, or enjoying a classical music concert. I have a general interest in method development, especially in chemistry (who would have guessed). This space is for general updates, utilising GitHub pages :)
        </p>
      </section>

      {/* Updates Section */}
      <section>
        <h2>Updates</h2>
        <ul className="update-list">
          {sortedUpdates.map((update, index) => (
            <li key={index}>
              <span className="date">{update.date}</span>
              <span className="heading">{update.heading}</span>
              <ReactMarkdown className="body">{update.body}</ReactMarkdown>
            </li>
          ))}
        </ul>
      </section>
    </div>
  );
}

export default App;
